.buttonCross {
    position: absolute;
    top: 12px;
    right: 12px;
    border: none;
    width: 16px;
    height: 16px;
    padding: 1px;
    background-color: transparent;
}

.buttonCross:hover {
    background-color: var(--greyF4);
}

.spanCancel {
    cursor: pointer;
    text-decoration: underline;
}

.content {
    width: 350px;
    height: 170px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
}
