.taskInput {
    width: 100%;
    font-size: 16px;
    height: 2rem;
    cursor: pointer;
    background-color: inherit;
    border-radius: 3px;
}

.taskLabel {
    width: 100%;
    font-size: 16px;
    cursor: pointer;
    background-color: inherit;
    text-align: left;
    overflow: hidden;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    display: -webkit-box;
}