.button {
    width: 32px;
    height: 32px;
    align-content: center;
    border: none;
    padding: 0;
    background-color: transparent;
}

.button:hover {
    background-color: transparent;
}

.contentWrapper {
    position: relative;
}

.image {
    width: 32px;
    height: 32px;
    cursor: pointer;
}

.text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}