.wrapper {
    text-align: left;
    max-width: 500px;
}

.list {
    list-style: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 33px;
    color: #333333;
}

list li {
    font-size: 16px;
    line-height: 33px;
    font-weight: 400;
    color: var(--black);
}

.list li:before {
    content: '\2022';
    color: red;
    font-weight: bold;
    display: inline-block;
    width: 1em;
    margin-left: -1em;
}
