@font-face {
    font-family: 'SF UI Display';
    src: url('../public/fonts/SFUIDisplay-Light.woff2') format('woff2'),
        url('../public/fonts/SFUIDisplay-Light.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: 'SF UI Display';
    src: url('../public/fonts/SFUIDisplay-Regular.woff2') format('woff2'),
        url('../public/fonts/SFUIDisplay-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'SF UI Display';
    src: url('../public/fonts/SFUIDisplay-Medium.woff2') format('woff2'),
        url('../public/fonts/SFUIDisplay-Medium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'SF UI Display';
    src: url('../public/fonts/SFUIDisplay-Semibold.woff2') format('woff2'),
        url('../public/fonts/SFUIDisplay-Semibold.woff') format('woff');
    font-weight: 600;
    font-style: normal;
}

@font-face {
    font-family: 'SF UI Display';
    src: url('../public/fonts/SFUIDisplay-Bold.woff2') format('woff2'),
        url('../public/fonts/SFUIDisplay-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'SF UI Display';
    src: url('../public/fonts/SFUIDisplay-Black.woff2') format('woff2'),
        url('../public/fonts/SFUIDisplay-Black.woff') format('woff');
    font-weight: 900;
    font-style: normal;
}

:root {
    --black: #333333;
    --white: #ffffff;
    --red: #dc3e22;
    --dark-red: #b7280f;
    --greyC4: #c4c4c4;
    --greyF4: #f4f4f4;
    --greyDE: #dedede;
    --grey99: #999999;
    --light-green: #a8b64f;
    --dark-green: #899441;
}

body {
    margin: 0;
    font-family: 'SF UI Display', 'sans-serif';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: linear-gradient(to bottom, #f8f8f8 0%, #ffffff 100%);
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
