.itemList {
    display: flex;
    min-height: 3.4rem;
    border-top: 1px solid #e3e3e3;
    align-items: center;
    padding-left: 0.4rem;
    padding-right: 0.4rem;
    gap: 0.4rem;
    max-width: 370px;
}

.itemList:hover {
    background-color: #e7e9eb;
}

.selected {
    background-color: #e7e9eb;
}
