.wrapper {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.area {
    background-color: var(--greyF4);
    display: flex;
    flex-direction: row;
    height: 450px;
}

.content {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
    margin: auto;
}