.wrapper {
    display: flex;
    width: 405px;
    height: 179px;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 25px;
    padding-right: 36px;
}

.wrapperContent {
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: left;
    align-self: center;
}

.image {
    width: 115px;
    height: 115px;
    align-self: center;
}

.focus {
    background-color: #ffdda9;
}

.pauseTime {
    background-color: #dfdcfe;
}

.numberOfStops {
    background-color: #c5f1ff;
}

.disabled {
    background-color: var(--greyF4);
}
