.header {
    display: flex;
    justify-content: space-between;
    padding: 0 80px;
    height: 70px;
    align-items: center;
    background-color: var(--white);
}

.logoContainer {
    display: flex;
    flex-direction: row;
    gap: 12px;
    align-items: center;
}

.logoText:hover {
    color: var(--dark-red);
}

.menuText:hover {
    color: var(--dark-red);
}

.logoImage {
    width: 40px;
    height: 40px;
}

.link {
    text-decoration: none;
    user-select: none;
}

.menuContainer {
    color: var(--red);
    fill: var(--red);
    display: flex;
    flex-direction: row;
    gap: 5px;
    align-items: center;
}

.menuContainer:hover {
    color: var(--dark-red);
    fill: var(--dark-red);
}
