.wrapper {
    width: 296px;
    height: 179px;
    background-color: var(--greyF4);
    display: flex;
    flex-direction: column;
}

.lazyPomidor {
    margin: auto;
}

.bottomContent {
    height: 51px;
    background-color: var(--red);
    display: flex;
}

.bottomText {
    margin: auto;
}

.topContent {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: auto;
    gap: 15px;
}
