.icon {
    height: 16px;
    width: 16px;
}

.ellipsis {
    height: 6px;
    width: 26px;
}

.diagramm {
    height: 24px;
    width: 24px;
}

.largeRound {
    height: 115px;
    width: 115px;
    fill: none;
    stroke-width: 5px;
    stroke-linecap: round;
    stroke-linejoin: round;
}

.lightGreen-fill {
    fill: var(--light-green);
}

.greyC4-fill {
    fill: var(--greyC4);
}

.grey99-fill {
    fill: var(--grey99);
}

.white-fill {
    fill: var(--white);
}

.black-fill {
    fill: var(--black);
}

.red-fill {
    fill: var(--red);
}

.darkRed-fill {
    fill: var(--dark-red);
}

.stroke{
    fill: none;
}

.lightGreen-stroke {
    stroke: var(--light-green);
}

.greyC4-stroke {
    stroke: var(--greyC4);
}

.grey99-stroke {
    stroke: var(--grey99);
}

.white-stroke {
    stroke: var(--white);
}

.black-stroke {
    stroke: var(--black);
}

.red-stroke {
    stroke: var(--red);
}

.darkRed-stroke {
    stroke: var(--dark-red);
}