.timeWithProlongateContainer {
    margin: auto;
    display: flex;
}

.timeContainer {
    width: 500px;
}

.stubContainer {
    width: 50px;
    display: flex;
}

.time {
    font-size: 150px;
    line-height: 179px;
    font-weight: 200;
    color: var(--black);
}

.startedText {
    color: var(--light-green);
}

.pauseText {
    color: var(--red);
}

.prolongTimeButton {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border: none;
    background-color: var(--greyC4);
    color: white;
    margin: auto;
    padding-top: 2px;
}

.prolongTimeButton:hover {
    background-color: var(--dark-green);
}
