.menuButton {
    width: 32px;
    height: 32px;
    line-height: 0;
    border: none;
    background-color: transparent;
    color: var(--greyC4);
    border-radius: 100px;
    padding: 0;
}

.menuButton:hover {
    background-color: var(--white);
}

.dropdown {
    background-color: white;
    border: 1px solid #c4c4c4;
    width: 165px;
}

.menuItem {
    padding: 12px 14px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
}

.menuItem > svg + span {
    margin-left: 10px;
}

.menuItem:hover {
    background-color: #f4f4f4;
}
